import React from "react";
import css from './Main.module.scss';

const Main = () => {
    return (<>
        <div className={css.root}>
            <div className={css.projectName}>
                Atlantis Launcher Wiki
            </div>
            <div>
                <div className={css.main}>Installation</div>
                <div className={css.content}>
                    Hello everyone. Thanks for using my launcher. To install it, follow the steps below. <br/>
                    Dependencies: <br/>
                    <ul>
                        <li>Pre-installed MySQL server.</li>
                        <li>Pre-installed Node.JS</li>
                    </ul>
                    First, go to my repository and download all with 'atlantis-*' name's.<br/>
                    You need to download it from 'dev' branch.<br/>
                    <br/>
                    Now create a folder and name it how you want.<br/>
                    Unzip all downloaded files into this folder. It must look like this:<br/>
                    <img className={css.img} src="https://i.imgur.com/woVdV9H.png" alt=""/>
                    <br/>
                    Install npm modules in each folder: 'npm i' from console in each folder.<br/>
                    <br/>
                    Now, i will tell what doing each folder. <br/>
                    <ul>
                        <li>'atlantis-server-*' : It's a server-side for launcher, needed to get info about servers, and send it to client.</li>
                        <li>'atlantis-client-*': It's a application, which will be builded with component below.</li>
                        <li>'atlantis-runtime-*' : It's a UI for launcher. Need to be builded and moved to folder 'UI' in component above.</li>
                    </ul>
                    <br/>
                    Now, let's start. Open a foler 'atlantis-runtime-*' and open '.env' file with your editor. <br/>
                    You can see 2 strings.
                    <ul>
                        <li>REACT_APP_PROJECT_NAME=Atlantis . Change Project Name to yours.</li>
                        <li>REACT_APP_HOST=http://IP:PORT/api . Change IP on your VDS hosting IP and port, on which will be started server-side (by default it's 3001)</li>
                    </ul>
                    So, now you can compile UI part. Open console on this folder and write 'npm run build'. Then wait when it's will be compiled. <br/>
                    If UI part compiled success, compiler creates a new folder called 'build'. Copy all files from this folder and place it on 'atlantis-client-*/UI' folder. <br/>
                    If UI folder not exists, create it. <br/>
                    <br/>
                    Now, you can try to compile client part. Open 'atlantis-client-*' folder, open console on it, and write 'npm run build'. <br/>
                    If compile success, you will get a path called 'release-builds', in which you will have 1 more folder, in which you can find a 'UI.exe'. <br/>
                    It's your compiled launcher. Now if you will try to launch it, you will get infinity loading, because you didn't start server-side.
                    <br/>
                    Now, we will start the server-side part. <br/>
                    Open folder 'atlantis-server-*'. In this folder you can find 'atlantis.sql'. Import this database into your MySQL server. <br/>
                    Now, open a config file, called 'config.json' in which you will find a DB settings and port, on which server will be launched. <br/>
                    Insert your settings. Then you can try to start server, open console and write 'node server.js'. <br/>
                    If connect to DB will be success, you will see it in console. <br/>
                    <br/>
                    Done! You created your launcher to ALT:V server!
                    <br/>
                    <br/>
                </div>
            </div>
                <div className={css.main}>How to add server?</div>
                <div className={css.content}>
                    Open for e.x. phpMyAdmin, open your database. You can find tables called 'server_data', open it. <br/>
                    It's will be had default values. <br/>
                    <ul>
                        <li>id : Don't touch. System value.</li>
                        <li>name : How will be called your server in launcher. Replace to your.</li>
                        <li>online : Don't touch. System value.</li>
                        <li>ip : Server ip. Replace to your.</li>
                        <li>port : Server port. Replace to your value.</li>
                    </ul>
                    After that, when you will configure your servers, and started server-side launcher should work! <br/>
                    <br/>
                    If you have any questions, or suggestion's, write it on mine official Discord channel:
                    <ul>
                        <li><a href="https://discord.gg/dYkjabMtgA">Discord</a></li>
                    </ul>
                </div>
            <div className={css.footer}>
                Atlantis Launcher &copy; SashaGoncharov19
            </div>
        </div>
    </>);
}

export default Main;